.carousel {
  width: 374px;
  height: 40px;
  border-radius: 5px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.numbers-container {
  scroll-snap-type: x mandatory;
  display: flex;
  overflow-x: auto;
}

.numbers {
  display: flex;
}

.number {
  width: 40px;
  height: 40px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  transition: background-color .3s;
  display: flex;
}

.arrow {
  width: 30px;
  height: 30px;
  text-align: center;
  cursor: pointer;
  background-color: #fff;
  font-size: 24px;
  line-height: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.left-arrow {
  width: 40px;
  height: 40px;
  background-color: #f7f7f7;
  border-radius: 5px;
  align-items: center;
  left: 10px;
}

.right-arrow {
  width: 40px;
  height: 40px;
  background-color: #f7f7f7;
  border-radius: 5px;
  align-items: center;
  right: 10px;
}

.number:hover {
  background-color: #ff6b08;
  border-radius: 5px;
}

.number.active {
  color: #fff;
  background-color: #ff6b08;
  border-radius: 5px;
}

p {
  width: 100%;
  height: 77px;
  text-align: center;
  background-color: #f7f7f7;
  padding-top: 50px;
}

